import { useState, useEffect } from 'react';
import { Play, Download, RefreshCw, AlertCircle, CheckCircle } from 'lucide-react';

export default function GitHubSimulation() {
  const [config, setConfig] = useState({
    num_agents: 10000,
    simulation_days: 180,
    spread_chance: 10,
    precaution_rate: 50,
    vaccination_rate: 80,
    num_replications: 5
  });
  
  const [workflowStatus, setWorkflowStatus] = useState(null);
  const [isRunning, setIsRunning] = useState(false);
  const [error, setError] = useState(null);
  const [configStatus, setConfigStatus] = useState(null);

  const GITHUB_TOKEN = process.env.REACT_APP_GITHUB_TOKEN;
  const REPO_OWNER = process.env.REACT_APP_GITHUB_OWNER;
  const REPO_NAME = process.env.REACT_APP_GITHUB_REPO;
  //console.log(GITHUB_TOKEN, REPO_OWNER, REPO_NAME);

  useEffect(() => {
    const missingVars = [];
    if (!GITHUB_TOKEN) missingVars.push('REACT_APP_GITHUB_TOKEN');
    if (!REPO_OWNER) missingVars.push('REACT_APP_GITHUB_OWNER');
    if (!REPO_NAME) missingVars.push('REACT_APP_GITHUB_REPO');

    if (missingVars.length > 0) {
      setConfigStatus({
        valid: false,
        message: `Missing environment variables: ${missingVars.join(', ')}`,
        missing: missingVars
      });
    } else {
      setConfigStatus({
        valid: true,
        message: 'Configuration loaded successfully',
        owner: REPO_OWNER,
        repo: REPO_NAME
      });
    }
  }, [GITHUB_TOKEN, REPO_OWNER, REPO_NAME]);

  const triggerWorkflow = async () => {
    if (!configStatus?.valid) {
      setError('Cannot trigger workflow: GitHub configuration is incomplete. Please check your .env.local file.');
      return;
    }

    setIsRunning(true);
    setError(null);
    
    try {
      console.log('Triggering workflow for:', `${REPO_OWNER}/${REPO_NAME}`);
      
      const response = await fetch(
        `https://api.github.com/repos/${REPO_OWNER}/${REPO_NAME}/actions/workflows/parallel-simulations.yml/dispatches`,
        {
          method: 'POST',
          headers: {
            'Accept': 'application/vnd.github.v3+json',
            'Authorization': `Bearer ${GITHUB_TOKEN}`,
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            ref: 'main',
            inputs: {
              num_agents: config.num_agents.toString(),
              simulation_days: config.simulation_days.toString(),
              spread_chance: config.spread_chance.toString(),
              precaution_rate: config.precaution_rate.toString(),
              vaccination_rate: config.vaccination_rate.toString(),
              num_replications: config.num_replications.toString()
            }
          })
        }
      );

      console.log('Response status:', response.status);

      if (response.ok) {
        setWorkflowStatus('triggered');
        setError(null);
        setTimeout(() => checkWorkflowStatus(), 2000);
      } else {
        const errorData = await response.json().catch(() => ({ message: response.statusText }));
        console.error('GitHub API Error:', errorData);
        
        let errorMessage = '';
        if (response.status === 401) {
          errorMessage = 'Authentication failed. Your GitHub token may be invalid or expired.\n\nSteps to fix:\n1. Go to GitHub Settings -> Developer Settings -> Personal Access Tokens\n2. Generate a new token with "workflow" and "repo" scopes\n3. Update REACT_APP_GITHUB_TOKEN in .env.local\n4. Restart your dev server (npm start)';
        } else if (response.status === 404) {
          errorMessage = `Not found. Please check:\n- Repository: ${REPO_OWNER}/${REPO_NAME}\n- Branch: main (exists?)\n- Workflow file: .github/workflows/parallel-simulations.yml (exists?)\n- Token has access to this repository`;
        } else if (response.status === 403) {
          errorMessage = 'Forbidden. Your token may not have the required permissions (workflow scope).';
        } else {
          errorMessage = `Failed to trigger workflow: ${errorData.message || response.statusText}`;
        }
        
        setError(errorMessage);
      }
    } catch (err) {
      console.error('Fetch error:', err);
      setError(`Network error: ${err.message}\n\nCheck your internet connection and GitHub API status.`);
    } finally {
      setIsRunning(false);
    }
  };

  const checkWorkflowStatus = async () => {
    if (!configStatus?.valid) return;

    try {
      const response = await fetch(
        `https://api.github.com/repos/${REPO_OWNER}/${REPO_NAME}/actions/runs?per_page=1`,
        {
          headers: {
            'Accept': 'application/vnd.github.v3+json',
            'Authorization': `Bearer ${GITHUB_TOKEN}`
          }
        }
      );

      const data = await response.json();
      if (data.workflow_runs && data.workflow_runs.length > 0) {
        const latestRun = data.workflow_runs[0];
        setWorkflowStatus(latestRun.status);
        
        if (latestRun.status === 'completed') {
          return latestRun.conclusion;
        }
      }
    } catch (err) {
      console.error('Error checking workflow status:', err);
    }
  };

  const downloadResults = async () => {
    if (!configStatus?.valid) return;

    try {
      const response = await fetch(
        `https://api.github.com/repos/${REPO_OWNER}/${REPO_NAME}/actions/runs?per_page=1`,
        {
          headers: {
            'Accept': 'application/vnd.github.v3+json',
            'Authorization': `Bearer ${GITHUB_TOKEN}`
          }
        }
      );

      const data = await response.json();
      if (!data.workflow_runs || data.workflow_runs.length === 0) {
        setError('No workflow runs found');
        return;
      }

      const latestRun = data.workflow_runs[0];
      
      const artifactsResponse = await fetch(latestRun.artifacts_url, {
        headers: {
          'Accept': 'application/vnd.github.v3+json',
          'Authorization': `Bearer ${GITHUB_TOKEN}`
        }
      });

      const artifacts = await artifactsResponse.json();
      
      if (artifacts.artifacts && artifacts.artifacts.length > 0) {
        artifacts.artifacts.forEach(artifact => {
          if (artifact.name === 'aggregated-results') {
            window.open(artifact.archive_download_url, '_blank');
          }
        });
      } else {
        setError('No artifacts found for this run');
      }
    } catch (err) {
      setError(`Error downloading results: ${err.message}`);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 p-8">
      <div className="max-w-6xl mx-auto">
        <div className="bg-white rounded-2xl shadow-xl p-8">
          <div className="flex items-center gap-4 mb-8">
            <div className="bg-blue-600 rounded-xl p-3">
              <svg className="w-8 h-8 text-white" fill="currentColor" viewBox="0 0 24 24">
                <path d="M3 13h8V3H3v10zm0 8h8v-6H3v6zm10 0h8V11h-8v10zm0-18v6h8V3h-8z"/>
              </svg>
            </div>
            <div>
              <h1 className="text-3xl font-bold text-gray-900">COVID-19 Agent-Based Model</h1>
              <p className="text-gray-600">GPU-Accelerated Epidemic Simulation with Long COVID</p>
            </div>
          </div>

          <div className="grid md:grid-cols-2 gap-8">
            <div>
              <h2 className="text-xl font-semibold mb-4 flex items-center gap-2">
                <span className="text-2xl"></span> Configuration
              </h2>
              
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Number of Agents
                  </label>
                  <input
                    type="number"
                    value={config.num_agents}
                    onChange={(e) => setConfig({...config, num_agents: Number(e.target.value)})}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Simulation Days
                  </label>
                  <input
                    type="number"
                    value={config.simulation_days}
                    onChange={(e) => setConfig({...config, simulation_days: Number(e.target.value)})}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Spread Chance: {config.spread_chance}%
                  </label>
                  <input
                    type="range"
                    min="0"
                    max="100"
                    value={config.spread_chance}
                    onChange={(e) => setConfig({...config, spread_chance: Number(e.target.value)})}
                    className="w-full"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Precaution Rate: {config.precaution_rate}%
                  </label>
                  <input
                    type="range"
                    min="0"
                    max="100"
                    value={config.precaution_rate}
                    onChange={(e) => setConfig({...config, precaution_rate: Number(e.target.value)})}
                    className="w-full"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Vaccination Rate: {config.vaccination_rate}%
                  </label>
                  <input
                    type="range"
                    min="0"
                    max="100"
                    value={config.vaccination_rate}
                    onChange={(e) => setConfig({...config, vaccination_rate: Number(e.target.value)})}
                    className="w-full"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Number of Parallel Replications
                  </label>
                  <input
                    type="number"
                    min="1"
                    max="20"
                    value={config.num_replications}
                    onChange={(e) => setConfig({...config, num_replications: Number(e.target.value)})}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  />
                </div>
              </div>

              <div className="mt-6 space-y-3">
                <button
                  onClick={triggerWorkflow}
                  disabled={isRunning || !configStatus?.valid}
                  className="w-full bg-blue-600 hover:bg-blue-700 disabled:bg-gray-400 text-white font-semibold py-3 px-6 rounded-lg flex items-center justify-center gap-2 transition"
                >
                  {isRunning ? (
                    <>
                      <RefreshCw className="w-5 h-5 animate-spin" />
                      Triggering...
                    </>
                  ) : (
                    <>
                      <Play className="w-5 h-5" />
                      Run Simulation on GitHub
                    </>
                  )}
                </button>

                <button
                  onClick={downloadResults}
                  disabled={workflowStatus !== 'completed'}
                  className="w-full bg-green-600 hover:bg-green-700 disabled:bg-gray-400 text-white font-semibold py-3 px-6 rounded-lg flex items-center justify-center gap-2 transition"
                >
                  <Download className="w-5 h-5" />
                  Download Results
                </button>
              </div>
            </div>

            <div>
              <h2 className="text-xl font-semibold mb-4">Status</h2>
              
              {configStatus && (
                <div className={`rounded-lg p-4 mb-4 ${
                  configStatus.valid 
                    ? 'bg-green-50 border border-green-200' 
                    : 'bg-yellow-50 border border-yellow-200'
                }`}>
                  <div className="flex items-start gap-3">
                    {configStatus.valid ? (
                      <CheckCircle className="w-5 h-5 text-green-600 flex-shrink-0 mt-0.5" />
                    ) : (
                      <AlertCircle className="w-5 h-5 text-yellow-600 flex-shrink-0 mt-0.5" />
                    )}
                    <div className="flex-1">
                      <p className={`font-semibold ${
                        configStatus.valid ? 'text-green-800' : 'text-yellow-800'
                      }`}>
                        {configStatus.valid ? 'Configuration Valid' : 'Configuration Incomplete'}
                      </p>
                      <p className={`text-sm mt-1 ${
                        configStatus.valid ? 'text-green-700' : 'text-yellow-700'
                      }`}>
                        {configStatus.message}
                      </p>
                      {configStatus.valid && (
                        <p className="text-xs text-gray-600 mt-2">
                          Repository: <span className="font-mono">{configStatus.owner}/{configStatus.repo}</span>
                        </p>
                      )}
                      {!configStatus.valid && (
                        <div className="mt-3 text-xs text-yellow-800 bg-yellow-100 p-3 rounded font-mono">
                          <p className="font-bold mb-2">Create .env.local file:</p>
                          <p>REACT_APP_GITHUB_TOKEN=GITHUB_TOKEN</p>
                          <p>REACT_APP_GITHUB_OWNER=REPO_OWNER</p>
                          <p>REACT_APP_GITHUB_REPO=REPO_NAME</p>
                          <p className="mt-2 font-bold">Then restart: npm start</p>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              )}
              
              {error && (
                <div className="bg-red-50 border border-red-200 rounded-lg p-4 mb-4">
                  <div className="flex items-start gap-3">
                    <AlertCircle className="w-5 h-5 text-red-600 flex-shrink-0 mt-0.5" />
                    <pre className="text-red-800 text-sm whitespace-pre-wrap flex-1">{error}</pre>
                  </div>
                </div>
              )}

              {workflowStatus && (
                <div className="bg-blue-50 border border-blue-200 rounded-lg p-6">
                  <div className="flex items-center gap-3 mb-4">
                    <div className={`w-3 h-3 rounded-full ${
                      workflowStatus === 'completed' ? 'bg-green-500' :
                      workflowStatus === 'in_progress' ? 'bg-blue-500 animate-pulse' :
                      workflowStatus === 'queued' ? 'bg-yellow-500' :
                      'bg-gray-500'
                    }`}></div>
                    <span className="font-semibold text-gray-900 capitalize">
                      {workflowStatus.replace('_', ' ')}
                    </span>
                  </div>
                  
                  <div className="text-sm text-gray-600 space-y-2">
                    <p>Running {config.num_replications} parallel simulations on GitHub Actions</p>
                    <p>Each simulation: {config.num_agents.toLocaleString()} agents  {config.simulation_days} days</p>
                  </div>
                </div>
              )}

              <div className="mt-6 bg-gray-50 rounded-lg p-6">
                <h3 className="font-semibold mb-3">How it works:</h3>
                <ol className="text-sm text-gray-700 space-y-2 list-decimal list-inside">
                  <li>Configure your simulation parameters</li>
                  <li>Click "Run Simulation on GitHub"</li>
                  <li>GitHub Actions runs multiple replications in parallel</li>
                  <li>Results are aggregated automatically</li>
                  <li>Download the combined results when complete</li>
                </ol>
              </div>
            </div>
          </div>
        </div>  
      </div>
    </div>
  );
}